footer{
    width: 100%;
    margin-top: 100px;
    padding-top: 40px;
    padding-bottom: 40px;
    background: #00467a;
    .md(margin-top , 50px);
    ul{
      display: flex;
      justify-content: flex-end;
      li{
        margin: 0px 20px;
        a{
          text-decoration: none;
          font-size: 18px;
          font-weight: 500;
          color: white;
          transition: all 100ms;
          display: block;
          padding: 5px 0px;
          overflow: hidden;
          position: relative;
          transition: all 500ms;
          &:hover {
            color: #f49d37;
          }
        }
      }
    }
    .tel{
      margin: 15px 0px;
      a{
        text-decoration: none;
        font-size: 18px;
        font-weight: 500;
        color: white;
        transition: all 100ms;
        display: block;
        padding: 5px 0px;
        overflow: hidden;
        position: relative;
        transition: all 500ms;
        text-align: right;
        letter-spacing: 6px;
        margin-right: 20px;
        &:hover {
          color: #f49d37;
        }
       
      }
    }
    
}