@import "./params/mix";
@import "./header";
@import "./section";
@import "./footer";
@import "./main";

img {
  max-width: 100%;
  height: auto;
}

body {
  min-width: 320px;
  font-family: "etelkReg";
  text-decoration: none;
}

.btn {
  border: none;
  background: transparent;
  color: #f49d37;
  border: 2px solid #f49d37;
  border-radius: 5px;
}

h2 {
  font-size: 35px;
  font-weight: 700;
  text-transform: capitalize;
  border-bottom: 6px solid #f49d37;
  color: #f49d37;
  padding: 15px 0px;
  margin-bottom: 50px;
  font-family: "etelkBold";
  .sm(font-size , 30px);
  .sm(text-align , center);
}
.fixed-nav {
  width: 100%;
  height: auto;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1111;
  margin-top: -30px;
}

.active-block {
  display: block !important;
}

.header-nav {
  max-width: 1500px;
  margin: auto;
  .lg-block({
    margin: 0px 10px;
  });
  .xl-block({
    margin: 0px ;
  });

  .nav-flex {
    display: flex;
    flex-wrap: wrap;
  }
  .nav-col-3 {
    width: calc(100% / 12 * 3);
    height: auto;
    margin: 0px 15px;
  }
  .nav-col-2 {
    width: calc(100% / 12 * 2);
    height: auto;
    margin: 0px 15px;
  }
  .nav-col-1 {
    width: calc(100% / 12 * 1);
    height: auto;
    margin: 0px 15px;
  }
  .nav-col-6 {
    width: calc(100% / 12 * 6);
    height: auto;
    margin: 0px 15px;
  }
  .nav-col-7 {
    width: calc(100% / 12 * 7);
    height: auto;
    margin: 0px 15px;
  }
}

.nav-lg-col-2 {
  .lg-block({
    width: calc(100% / 12 * 2) !important;
    height: auto;
    margin: 0px 15px;
  });
}

.nav-xl-col-1 {
  .xl-block({
    width: calc(100% / 12 * 1) ;
    height: auto;
    margin: 0px 15px;
  });
}

.nav-md-col-12 {
  .md-block({
    width: calc(100% / 12 * 12) !important ;
    height: auto;
    margin: 0px 15px;
  });
}
.nav-md-col-5 {
  .md-block({
    width: calc(100% / 12 * 5) !important ;
    height: auto;
    margin: 0px 15px;
  });
}
.nav-md-col-8 {
  .md-block({
    width: calc(100% / 12 * 8) !important ;
    height: auto;
    margin: 0px 15px;
  });
}
.nav-sm-col-4 {
  .sm-block({
    width: calc(100% / 12 * 4) !important ;
    height: auto;
    margin: 0px 15px;
  });
}
.nav-sm-col-12 {
  .sm-block({
    width: calc(100% / 12 * 12) !important ;
    height: auto;
    margin: 0px 15px;
  });
}
